.App {
  text-align: center;
  display: flex;
    flex-direction: column;
    align-items: center;
    height: calc(100vh - 100px);
    padding-top: 100px;
    background-color: aliceblue;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

input:focus-visible {
  outline: 0px;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.paginationButtons {
  margin-right: 20px;
    border-radius: 30px;
    background-color: white;
    color: black;
    font-weight: 900;
    height: 30px;
    width: 33px;
    padding: 5px;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
